import React from "react";
// next
import Image from "next/image";
// mui
import { Box, Typography, useMediaQuery, useTheme } from "@mui/material";

function ProjectAndLabCard({ image, description }) {
  const theme = useTheme();
  const isMdOrLarger = useMediaQuery(theme.breakpoints.up("md"));

  return (
    <Box sx={{ bgcolor: "secondary.main", width: isMdOrLarger ? 400 : 280 }}>
      <Image
        src={image}
        alt="project"
        style={{ height: "40vh", width: isMdOrLarger ? 400 : 280 }}
      />
      <Box sx={{ display: "flex", justifyContent: "center", paddingY: 1 }}>
        <Typography variant="subtitle1" sx={{ color: "grey.0", width: "90%" }}>
          {description}
        </Typography>
      </Box>
    </Box>
  );
}

export default ProjectAndLabCard;
